@import './utils/variables';

.rune-selection {
  padding: $spacing-xxl;
  min-height: 60vh;

  h2 {
    color: $color-primary;
    font-size: $font-size-xxl;
    font-weight: $font-weight-bold;
    margin-bottom: $spacing-md;
    text-align: center;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
  }

  .instruction {
    text-align: center;
    color: $color-text-dark;
    font-size: $font-size-base;
    margin-bottom: $spacing-xl;
    font-style: italic;
  }

  .rune-grid {
    display: flex;
    flex-wrap: wrap;
    gap: $spacing-lg;
    justify-content: center;
    margin-bottom: $spacing-xl;
  }

  .rune-stone {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    cursor: pointer;
    transition: all $transition-normal;
    
    // Effet pierre grise
    background: linear-gradient(135deg, #6b6b6b 0%, #4a4a4a 50%, #5a5a5a 100%);
    box-shadow: 
      inset -3px -3px 8px rgba(0, 0, 0, 0.4),
      inset 3px 3px 8px rgba(255, 255, 255, 0.2),
      0 8px 16px rgba(0, 0, 0, 0.3);

    .rune-symbol {
      font-size: 48px;
      color: $color-accent;
      text-shadow: 
        1px 1px 2px rgba(0, 0, 0, 0.8),
        0 0 10px rgba(212, 175, 55, 0.5);
      font-weight: bold;
    }

    .rune-state {
      position: absolute;
      top: 5px;
      right: 5px;
      font-size: 20px;
      color: $color-accent;
      text-shadow: 0 0 5px rgba(212, 175, 55, 0.8);
    }

    &:hover {
      transform: translateY(-8px) scale(1.08);
      box-shadow: 
        inset -3px -3px 8px rgba(0, 0, 0, 0.4),
        inset 3px 3px 8px rgba(255, 255, 255, 0.2),
        0 12px 24px rgba(0, 0, 0, 0.4),
        0 0 30px rgba(212, 175, 55, 0.3);
      
      .rune-symbol {
        text-shadow: 
          1px 1px 2px rgba(0, 0, 0, 0.8),
          0 0 15px rgba(212, 175, 55, 0.8);
      }
    }

    &.selected {
      background: linear-gradient(135deg, #8B4513 0%, #6d3410 50%, #7a3d12 100%);
      box-shadow: 
        inset -3px -3px 8px rgba(0, 0, 0, 0.5),
        inset 3px 3px 8px rgba(255, 255, 255, 0.1),
        0 8px 16px rgba(0, 0, 0, 0.4),
        0 0 30px rgba(212, 175, 55, 0.6);
      
      .rune-symbol {
        color: $color-accent;
        text-shadow: 
          1px 1px 2px rgba(0, 0, 0, 0.9),
          0 0 20px rgba(212, 175, 55, 1);
      }
    }
  }

  .rune-info {
    max-width: 600px;
    margin: 0 auto $spacing-xl;
    padding: $spacing-xl;
    background: linear-gradient(135deg, rgba(75, 0, 130, 0.1) 0%, rgba(139, 69, 19, 0.1) 100%);
    border-radius: $border-radius-lg;
    border: 2px solid $color-accent;
    box-shadow: $box-shadow-md;
    text-align: center;
    min-height: 150px;

    .selected-runes-info {
      h3 {
        color: $color-primary;
        font-size: $font-size-xl;
        font-weight: $font-weight-bold;
        margin-bottom: $spacing-lg;
      }

      .selected-list {
        display: flex;
        flex-direction: column;
        gap: $spacing-md;
      }

      .selected-rune-detail {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: $spacing-md;
        background-color: rgba(255, 255, 255, 0.5);
        border-radius: $border-radius-md;
        border-left: 4px solid $color-accent;

        .symbol {
          font-size: 32px;
          color: $color-accent;
          min-width: 50px;
        }

        .name {
          flex: 1;
          font-weight: $font-weight-bold;
          color: $color-primary;
          font-size: $font-size-lg;
          text-align: left;
          margin-left: $spacing-md;
        }

        .state {
          color: $color-secondary;
          font-size: $font-size-base;
          font-weight: $font-weight-bold;
        }
      }
    }
  }

  .navigation-buttons {
    margin-top: $spacing-xxl;
    display: flex;
    justify-content: space-between;
    gap: $spacing-md;

    button {
      background: linear-gradient(135deg, $color-secondary 0%, #6d3410 100%);
      color: $color-text-white;
      padding: $spacing-md $spacing-xl;
      border: none;
      border-radius: $border-radius-md;
      cursor: pointer;
      font-size: $font-size-base;
      font-weight: $font-weight-bold;
      box-shadow: $box-shadow-sm;
      transition: all $transition-normal;

      &:hover {
        background: linear-gradient(135deg, $color-hover 0%, #8f4519 100%);
        transform: translateY(-2px);
        box-shadow: $box-shadow-md;
      }

      &:active {
        transform: translateY(0);
      }
    }
  }
}

@media (max-width: $breakpoint-tablet) {
  .rune-selection {
    padding: $spacing-lg;

    h2 {
      font-size: $font-size-xl;
    }

    .rune-grid {
      gap: $spacing-md;
    }

    .rune-stone {
      width: 80px;
      height: 80px;

      .rune-symbol {
        font-size: 36px;
      }

      .rune-state {
        font-size: 16px;
      }
    }

    .rune-info {
      padding: $spacing-lg;

      .selected-rune-detail {
        flex-direction: column;
        text-align: center;
        gap: $spacing-sm;

        .name {
          text-align: center;
          margin-left: 0;
        }
      }
    }

    .navigation-buttons {
      flex-direction: column;
      gap: $spacing-md;

      button {
        width: 100%;
      }
    }
  }
}
