@import './utils/variables';

.payment {
  background-color: $color-background;
  padding: $spacing-xxl;
  border-radius: $border-radius-xl;
  box-shadow: $box-shadow-lg;
  max-width: 600px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 50vh;

  h2 {
    color: $color-primary;
    font-size: $font-size-xxl;
    font-weight: $font-weight-bold;
    margin-bottom: $spacing-lg;
    text-align: center;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
  }

  p {
    font-size: $font-size-lg;
    color: $color-text-white;
    margin-bottom: $spacing-xl;
    text-align: center;
    line-height: 1.6;
  }

  .payment-buttons {
    display: flex;
    gap: $spacing-lg;
    margin-bottom: $spacing-xl;
    flex-wrap: wrap;
    justify-content: center;

    button {
      background: linear-gradient(135deg, $color-secondary 0%, darken($color-secondary, 10%) 100%);
      color: $color-text-white;
      border: 2px solid $color-accent;
      padding: $spacing-md $spacing-xl;
      font-size: $font-size-base;
      font-weight: $font-weight-bold;
      border-radius: $border-radius-lg;
      cursor: pointer;
      transition: all $transition-normal;
      box-shadow: $box-shadow-sm;

      &:hover {
        background: linear-gradient(135deg, $color-hover 0%, darken($color-hover, 10%) 100%);
        transform: translateY(-2px);
        box-shadow: $box-shadow-md;
      }

      &:active {
        transform: translateY(0);
      }
    }
  }

  > button {
    background-color: transparent;
    border: 2px solid $color-accent;
    color: $color-accent;
    padding: $spacing-md $spacing-lg;
    border-radius: $border-radius-lg;
    font-size: $font-size-sm;
    font-weight: $font-weight-medium;
    cursor: pointer;
    transition: all $transition-normal;

    &:hover {
      background: linear-gradient(135deg, $color-accent 0%, darken($color-accent, 10%) 100%);
      color: $color-text-dark;
      transform: translateY(-2px);
      box-shadow: $box-shadow-sm;
    }
  }

  @media (max-width: $breakpoint-mobile) {
    padding: $spacing-lg;

    h2 {
      font-size: $font-size-xl;
    }

    p {
      font-size: $font-size-base;
    }

    .payment-buttons {
      flex-direction: column;
      gap: $spacing-md;
      width: 100%;

      button {
        width: 100%;
      }
    }

    > button {
      width: 100%;
      margin-top: $spacing-md;
    }
  }
}
